import md5 from "@/common/SDK/md5.min.js";
export default {
	data() {
		return {
			email: "",
			code: '',
			passwd: "",
			Status:"",
			getCodeText: '获取验证码',
			getCodeBtnColor: "#ffffff",
			getCodeisWaiting: false
		}
	},
	onLoad() {

	},
	methods: {
		Timer() {},
		//这是请求验证码的方法
		getCode() {
			uni.hideKeyboard()
			if (this.getCodeisWaiting) {
				return;
			}
			if (!(/^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/.test(this.email))) {
				uni.showToast({
					title: '请填写正确邮箱',
					icon: "none"
				});
				return false;
			} else {
				uni.request({
					url: 'http://120.79.19.253:10087/M',
					data: {
						email: this.email
					},
					success: (res) => {
						console.log(res.data);
						this.text = 'request success';
						if (res.data.status == 1) {
							this.getCodeText = "发送中..."
							this.getCodeisWaiting = true;
							this.getCodeBtnColor = "rgba(255,255,255,0.5)"
							setTimeout(() => {
								uni.showToast({
									title: '验证码已发送',
									icon: "none"
								});
								//示例默认1234，生产中请删除这一句。
								this.setTimer();
							}, 1000)
						}

					}
				})
			}

		},
		setTimer() {
			let holdTime = 60;
			this.getCodeText = "重新获取(60)"
			this.Timer = setInterval(() => {
				if (holdTime <= 0) {
					this.getCodeisWaiting = false;
					this.getCodeBtnColor = "#ffffff";
					this.getCodeText = "获取验证码"
					clearInterval(this.Timer);
					return;
				}
				this.getCodeText = "重新获取(" + holdTime + ")"
				holdTime--;

			}, 1000)
		},
		doReg() {
			uni.hideKeyboard()
			//模板示例部分验证规则
			if (!(/^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/.test(this.email))) {
				uni.showToast({
					title: '请填写正确邮箱',
					icon: "none"
				});
				return false;
			}
			//示例验证码，实际使用中应为请求服务器比对验证码是否正确。
			if (!(/^\w{6}$/.test(this.code))) {
				uni.showToast({
					title: '验证码不正确',
					icon: "none"
				});
				return false;
			}
			if (!(/^(\w){6,10}$/.test(this.passwd))) {
				uni.showToast({
					title: '密码不正确',
					icon: "none"
				});
				return false;
			}
			uni.showLoading({
				title: '提交中...'
			})
			var data = {
				name:"新用户",
				pwd: md5(this.passwd),
				email:this.email,
				yzm:this.code
			};
			uni.request({
			    url: 'http://120.79.19.253:10086/Regist', //仅为示例，并非真实接口地址。
			    data,
			    success: (result) => {
			        this.text = 'request success';
					this.Status= result.data.status;
					if(this.Status==1)
					{
						var token=result.data.token;
						uni.setStorage({
							key: 'token',
							data: token,
							success: function() {
								uni.showToast({
									title: '注册成功',
									position: "center",
									image: "../../static/img/mysuccess.png"
								});
								uni.switchTab({
								    url: '/pages/tabBar/home/home'
								});
							},
							fail: function() {
								uni.showToast({
									title: '用户数据写入失败，建议重启本应用',
									position: "center",
									image: "../../static/img/myfail.png"
								})
							}
						})
					}else if(this.Status==2)
					{
						uni.showToast({
							title: '邮箱的验证码出错',
							position: "center",
							image: "../../static/img/myfail.png"
						})
					}else if(this.Status==3)
					{
						uni.showToast({
							title: '邮箱被占用',
							position: "center",
							image: "../../static/img/myfail.png"
						})
					}
			    }
			});
		},

		toLogin() {
			uni.hideKeyboard()
			uni.redirectTo({
				url: 'login'
			});
			uni.navigateBack();
		}
	}
}
